/* Contact */
.section__contact {
  background: $dark_blue;
  height: 100vh;
  overflow: hidden;
  color: $white;
  text-align: center;
  padding: 100px 0;
  ul {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    width: 500px;
    align-items: center;
    text-align: center;
    margin: 50px auto 0;
    font-size: 0;
    li,
    a {
      display: inline-block;
    }
    a {
      font-size: 145px;
      color: inherit;
      text-decoration: none;
      font-weight: 800;
    }
    .icon {
      width: 130px;
      height: auto;
      &__linkedin {
        width: 110px;
      }
      &__instagram {
        width: 105px;
      }
      path,
      circle {
        fill: $white;
      }
    }
  }
}

