/* Experience */
.section__work {
  background: $yellow;
  overflow: hidden;
  color: $white;
  padding-top: 100px;
  padding-bottom: 100px;
  min-height: 100vh;
  .section__title {
    position: relative;
    padding-bottom: 20px;
    &:after {
      content: '';
      display: inline-block;
      height: 3px;
      width: 0;
      background: $white;
      position: absolute;
      bottom: 0;
      left: 0;
      transition: width 0.6s ease 1s;
    }
    // &.animated {
    //   &:after {
    //     width: 100%;
    //   }
    // }
  }
  p {
    + p {
      margin-top: 10px;
    }
  }
  .section__description {
    width: 600px;
    margin-bottom: 50px;
    position: relative;
    padding-right: 30px;
    padding-top: 20px;
    padding-bottom: 20px;
    &:before,
    &:after {
      content: '';
      display: inline-block;
      background: $white;
      position: absolute;
      right: 0;
    }
    &:after {
      width: 3px;
      height: 0;
      top: 0;
      transition: height 0.6s ease 1.3s;
    }
    &:before {
      height: 3px;
      width: 0;
      bottom: 0;
      transition: width 0.6s ease 1.8s;
    }
    // &.animated {
    //   &:after {
    //     height: 100%;
    //   }
    //   &:before {
    //     width: 198px;
    //   }
    // }
  }
  .section__list {
    width: 400px;
    margin-left: 400px;
    li {
      font-size: 16px;
      line-height: 24px;
      + li {
        margin-top: 20px;
      }
    }
  }
  .section__subtitle {

  }
}

